.game-board {
  background: #ECECEC;
  border-radius: 5px;
}

.snake {
  background: gray;
  border-radius: 5px;
  border: 1px solid #333333;
}

.food {
  border-radius: 5px;
  border: 1px solid #333333;
}

.result > span {
  margin-right: 20px;
  font-size: 20px;
  font-weight: 500;
}



